<template>

    <div class="paginataion-container">
        <span class="total-num">共&nbsp;{{issues.length}}&nbsp;条</span>
        <img 
            :class="lastCurrentPage === 1 ?'pre-page-disabled':'pre-page'"
            @click="selectPage(parseInt(page) - 1)"
        >
        <input 
            class="current-page" 
            type="number"
            v-model="page"
            @keyup.enter="handleCurrentPageChanged()"
        >
        <span class="page-count">/&nbsp;&nbsp;&nbsp;{{pageLeng}}</span>
        <img 
            :class="lastCurrentPage === pageLeng ? 'next-page-disabled':'next-page'"
            @click="selectPage(parseInt(page) + 1)"
        >
    </div>

</template>
<script>
    export default {
        props:{
            issues:{
                type:Array,
                required:true
            },
            pageLeng:{
                type:Number,
                required:false,
                validator (val) {
                return val >= 0
                }
            },
            currentPage:{
                type:Number,
                required:true
            }
        },
        data() {
            return {
                lastCurrentPage: 1,
                page:1
            }
        },
        methods:{

            selectPage (newCurrentPage) {
                this.page = newCurrentPage
                this.handleCurrentPageChanged()
            },
            handleCurrentPageChanged () {
                let newCurrentPage = parseInt(this.page)
                if (newCurrentPage <= 0) {
                    this.page = newCurrentPage = 1
                }
                if (newCurrentPage > this.pageLeng) {
                    this.page = newCurrentPage = this.pageLeng
                }
                if (newCurrentPage !== this.lastCurrentPage) {
                    this.lastCurrentPage = newCurrentPage
                    this.$emit('currentPageChanged', parseInt(newCurrentPage))
                }
      }
        },
        watch: {
            currentPage (val) {
                this.lastCurrentPage = this.currentPage;
                this.page = this.currentPage;
            }
        },
        mounted () {
            this.$nextTick(() => {
                this.lastCurrentPage = this.currentPage;
                this.page = this.currentPage;
            })
        }
    }
</script>
<style>
</style>


