<template>
    <div class="blog-container">
        <!-- 列表 -->
        <labels-list @labelListFiltrate="labelListFiltrate"/>
        <!-- 主视图 -->
        <div class="blog-list-container">
            <div class="table-header"><span>名称</span> <span>创建时间</span></div>
            <ul class="issue-list" v-if="issues && issues.length > 0" v-scrollBar>
                <li class="issue-item" v-for="(item, index) in computedIssues" :key="index">
                    <router-link :to="{path:'/noteDetail', query: {id: item.id}}">
                        {{item.title}}
                    </router-link>
                    <ul>
                        <li class="tag tag-small" v-for="(items, index) in item.label" :key="index" :style="{backgroundColor: '#'+ items.color}">
                            {{items.name}}
                        </li>
                    </ul>
                    <span class="time">
                        2017-11-20 20:43
                    </span>
                </li>
            </ul>
            <div v-else class="no-data">
                <div>
                    <img src="../../static/images/no-data.png" alt="">
                </div>
                <span>该分类下还没有文章哦</span>
            </div>
            <div class="bottom-bar">
                <input type="text" placeholder="按文章标题或内容搜索..." class="fl query">
                <pagination-model :issues="issues" :pageLeng="pageLeng" :currentPage="currentPage" @currentPageChanged="currentPageChanged">
                </pagination-model>
            </div>
        </div>

    </div>
</template>
<script>
    import LabelsList from '../components/LabelList.vue'

    import PaginationModel from '../components/Pagination.vue'
    export default {
        data() {
            return {
                selectLabel:'ALL',
                issues: [{
                    id: 0,
                    title: '这是 HTML 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:0,
                        id: 0,
                        name: 'HTML',
                        color: 'B60205'
                    }]
                }, {
                    id: 1,
                    title: '这是 CSS 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:1,
                        id: 0,
                        name: 'CSS',
                        color: 'FBCA04'
                    }]
                }, {
                    id: 2,
                    title: '这是 JavaScript 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:2,
                        id: 0,
                        name: 'JAVASCRIPT',
                        color: 'B60205'
                    }]
                }, {
                    id: 3,
                    title: '这是 axios 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:3,
                        id: 0,
                        name: 'Axios',
                        color: 'B60205'
                    }]
                }, {
                    id: 4,
                    title: '这是 es6 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:4,
                        id: 0,
                        name: 'ES6',
                        color: 'B60205'
                    },{
                        parentId:4,
                        id: 0,
                        name: 'JavaScript',
                        color: 'B60205'
                    }]
                }],

                pageSize: 3,
                currentPage: 1,

                 issuess: [{
                    id: 0,
                    title: '这是 HTML 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:0,
                        id: 0,
                        name: 'HTML',
                        color: 'B60205'
                    }]
                }, {
                    id: 1,
                    title: '这是 CSS 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:1,
                        id: 0,
                        name: 'CSS',
                        color: 'FBCA04'
                    }]
                }, {
                    id: 2,
                    title: '这是 JavaScript 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:2,
                        id: 0,
                        name: 'JAVASCRIPT',
                        color: 'B60205'
                    }]
                }, {
                    id: 3,
                    title: '这是 axios 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:3,
                        id: 0,
                        name: 'Axios',
                        color: 'B60205'
                    }]
                }, {
                    id: 4,
                    title: '这是 es6 的标题',
                    time: '2018-08-30',
                    label: [{
                        parentId:4,
                        id: 0,
                        name: 'ES6',
                        color: 'B60205'
                    },{
                        parentId:4,
                        id: 0,
                        name: 'JavaScript',
                        color: 'B60205'
                    }]
                }],
            }
        },
        computed: {
            computedIssues() {
                return this.issues.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
            },
            //计算总页数
            pageLeng() {
                if(Math.ceil(this.issues.length / this.pageSize) == 0){
                    return 1
                }else{
                    return Math.ceil(this.issues.length / this.pageSize)
                }
            }
        },
        methods: {
            currentPageChanged(val) {
                this.currentPage = val
            },
            labelListFiltrate(val){
                let newList=[];
                if(val.toUpperCase() == 'ALL'){
                    this.issues = this.issuess;
                }else{
                    this.issuess.forEach(element => {
                        element.label.filter(item=>{
                            if(item.name.toUpperCase() == val.toUpperCase()){
                                newList.push(element)
                            }
                        })
                    });
                    this.issues = newList;
                }
            }
        },
        components: {
            'pagination-model': PaginationModel,
            'labels-list': LabelsList
        }
    }
</script>
<style>
 
</style>


