<template>
    <ul class="label-list">
        <li @click="labelListFiltrate('all')">
            <span class="tag" 
                :class="activeLabelName.toUpperCase() == 'ALL'?'':'tag-unchecked'" 
                :style="activeLabelName.toUpperCase() == 'ALL'?{ backgroundColor: '#3593f2'}:''"
            >
                All
            </span>

        </li>
        <li>
            <span 
                v-for="(item, index) in labelList" 
                :key="index" 
                class="tag"
                :class="activeLabelName == item.name.toUpperCase()?'':'tag-unchecked'" 
                :style="activeLabelName == item.name.toUpperCase()?{ backgroundColor: '#' + item.color}:''"
                @click="labelListFiltrate(item.name)"
                >{{item.name}}</span>
        </li>
    </ul>
</template>
<script>
    export default {
        data(){
            return {
                activeLabelName:'All',
                labelList:[{
                    id:0,
                    name:'Html',
                    color:'B60205'
                },{
                    id:1,
                    name:'css',
                    color:'FBCA04'
                },{
                    id:2,
                    name:'JavaScript',
                    color:'B60205'
                },{
                    id:3,
                    name:'Vue',
                    color:'B60205'
                },{
                    id:4,
                    name:'ES6',
                    color:'B60205'
                },{
                    id:5,
                    name:'Axios',
                    color:'0052CC'
                }]
            }
        },
        methods:{
            labelListFiltrate(val){
                this.activeLabelName = val.toUpperCase();
                this.$emit("labelListFiltrate",val)
            }
        }
    }
</script>

<style>
</style>


